@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap");

#root, body {
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
  overflow-x: hidden;
}

.homepage {
  overflow: hidden;
}

*:focus {outline:0;}

.wrapper {
  padding: 0 200px;
}

.blue-btn {
  border: none;
  display: flex;
  color: #fff;
  align-items: center;
  width: 165px;
  height: 50px;
  border-radius: 50px;
  background-color: #3D37F1;
  justify-content: center;
  font-size: 16px;
  margin-top: 1.5rem;
  box-shadow: #7570f4 1px 2px 11px 0px;

  .wave-icon {
    height: 25px;
    margin-right: 0.5rem;
  }

  &:hover {
    cursor: pointer;
  }
}

.logo {
  color: #4B647E;
  font-weight: 300;
  font-size: 22px;

  span {
    color: #3D37F1;
    font-weight: 700;
  }
}

.navigation {
  display: flex;
  position: relative;
  align-items: center;
  padding-top: 3rem;
  justify-content: space-between;

  .nav-menu {
    display: flex;
    z-index: 2;
    margin: 0;
    padding: 0;
    align-items: center;
    list-style-type: none;

    .nav-item {
      color: #fff;
      font-size: 16px;
      font-weight: 700;
      margin-right: 1.5rem;
      text-transform: uppercase;
      transition: color ease 350ms;

      &:hover {
        cursor: pointer;
        color: #FF8923;
      }
    }

    .orange-btn {
      border: none;
      color: #fff;
      height: 40px;
      width: 110px;
      font-weight: 800;
      border-radius: 50px;
      background-color: #FF8923;
      box-shadow: #FF8923 0 3px 10px 0px;

      &:hover {
        cursor: pointer;
      }
    }
  }

  .nav-waves {
    top: -14px;
    right: -340px;
    height: 350px;
    opacity: 0.9;
    position: absolute;
  }
}

.header {
  display: flex;
  align-items: center;
  height: 900px;

  .left {
    flex: 1;

    .welcome-text {
      .title {
        margin: 0;
        font-size: 60px;
        color: #534D4D;
        font-weight: 800;
      }

      .desc {
        margin: 0;
        color: #534D4D;
        font-size: 17px;
        margin-top: 1rem;
      }
    }

  }

  .right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    .header-illustration {
      opacity: 0.9;
      width: 85%;
    }
  }
}


.services-section {
  padding: 5rem;
  background-color: #FBFCFD;

  .highlight-title {
    font-size: 15px;
    font-weight: bold;
    color: #3D37F1;
    text-align: center;
  }

  .title {
    color: #5A5454;
    font-size: 15px;
    text-align: center;
    font-size: 30px;
    font-weight: normal;
    margin: 0;
    padding: 0;
  }

  .desc {
    margin: 0;
    padding: 0;
    margin-top: 1rem;
    color: #747474;
    text-align: center;
  }

  .services-list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 3rem;
    flex-direction: column;
    justify-content: space-evenly;

    .row {
      flex: 1;
      display: flex;
      justify-content: center;
    }

    .service-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 1.5rem;
      flex-basis: 250px;
      margin-right: 1rem;
      height: 265px;

      .service-icon {
        height: 57px;
      }

      .service-title {
        color: #fff;
        font-size: 18px;
        margin: 0;
        margin-top: 1rem;
        font-weight: normal;
      }

      .service-desc {
        color: #fff;
        font-size: 14px;
        text-align: center;
      }

      .service-btn {
        background: transparent;
        border: 2px solid #fff;
        height: 40px;
        width: 110px;
        border-radius: 50px;
        color: #fff;
        margin-top: 2rem;

        &:hover {
          cursor: pointer;
          background: #fff;
          color: #4B647E;
        }
      }
    }
  }
}

.aboutus {
  display: flex;
  align-items: center;
  padding: 5rem;

  .aboutus-ill {
    width: 550px;
  }

  .left {
    display: flex;
    flex-basis: 60%;
    align-items: center;
    justify-content: center;
  }

  .right {
    display: flex;
    flex-basis: 40%;
    flex-direction: column;
    justify-content: center;

    .title {
      margin: 0;
      padding: 0;
      color: #5A5454;
      font-weight: bold;
    }

    .desc {
      margin: 0;
      padding: 0;
      margin-top: 1rem;
      color: #747474;
    }

    .play-section {
      display: flex;
      margin-top: 2rem;
      align-items: center;

      .circle-blue-btn {
        width: 50px;
        height: 50px;
        border: none;
        display: flex;
        border-radius: 50%;
        align-items: center;
        justify-content: center;
        background-color: #3D37F1;

        .play-ill {
          height: 15px;
          margin-left: 2px;
        }

        &:hover {
          cursor: pointer;

        }
      }

      .splitter {
        height: 3px;
        width: 50px;
        opacity: 0.5;
        border-radius: 50px;
        margin-left: 1rem;
        background: #686868;
      }

      .desc {
        margin: 0;
        padding: 0;
        font-weight: 300;
        color: #686868;
        margin-left: 1rem;
      }

    }

  }
}


.our-success {
  padding: 5rem;
  
  .title {
    margin: 0;
    padding: 0;
    color: #5A5454;
    font-size: 15px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
  }

  .desc {
    margin: 0;
    padding: 0;
    margin-top: 0.5rem;
    color: #747474;
    text-align: center;
  }

  .success-list {
    margin-top: 3rem;
    display: flex;
    justify-content: center;

    .box {
      width: 200px;
      height: 200px;
      display: flex;
      align-items: center;
      border-radius: 40px;
      flex-direction: column;
      justify-content: center;
      background-color: #fff;
      margin: 0 1.3rem;
      box-shadow: rgba(197, 195, 249, 0.35) 1px 1px 9px;

      .inner-box {
        height: 100px;
        display: flex;
        width: 100px;
        color: #fff;
        font-size: 20px;
        font-weight: 900;
        border-radius: 20px;
        align-items: center;
        background-color: red;
        justify-content: center;
      }

      .title {
        margin: 0;
        padding: 0;
        font-size: 12px;
        color: #4B647E;
        font-weight: 900;
        margin-top: 1rem;
      }
    }
  }

}


.testimonials-section {
  padding: 5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  // height: 500px;
  background-color: #0C0F32;
  
  .title {
    margin: 0;
    padding: 0;
    color: #fff;
    font-size: 15px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
  }

  .desc {
    margin: 0;
    padding: 0;
    margin-top: 0.5rem;
    color: #fff;
    opacity: 0.3;
    text-align: center;
  }

  .testimonials-bg {
    // height: 400px;
    position: absolute;
    top: 131px;
    left: 8%;
    filter: blur(2px);
  }

  .info {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 7rem;


    .avatar {
      height: 100px;
    }

    .message {
      margin: 2rem 0;
      padding: 0;
      color: #fff;
      font-size: 14px;
      text-align: center;
    }

    .name {
      margin: 0;
      padding: 0;
      color: #8F8BFF;
      font-size: 18px;
      font-weight: 800;
    }

    .job {
      margin: 0;
      padding: 0;
      font-size: 17px;
      color: #fff;
      font-weight: 500;
    }
  }

  .learnmore {
    height: 200px;
    margin-top: 15rem;
    // width: 1000px;
    display: flex;
    width: 50%;
    border-top: 1px solid #707070;

    .left {
      flex: 1;
      display: flex;
      justify-content: center;
      flex-direction: column;

      .title {
        padding: 0;
        margin: 0;
        text-align: left;
        color: #fff;
        opacity: 0.35;
        font-size: 13px;
        font-weight: normal;
      }

      .desc-memo {
        margin: 0;
        padding: 0;
        text-align: left;
        color: #fff;
        margin-top: 1rem;
        font-weight: normal;
      }
    }

    .right {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: center;


      button {
        box-shadow: none;
        margin: 0;
        height: 67px;
        width: 300px;
        font-size: 18px;

        .wave-icon {
          margin-right: 1rem;
        }
      }
    }
  }
}

.phases-sections {
  padding: 5rem;
  display: flex;
  justify-content: center;
  background: #FBFCFD;

  .phase-block {
    height: 400px;
  }
}

.workflow {
  padding: 5rem;
  display: flex;

  .left {
    flex: 1;
    display: flex;
    justify-content: center;

    .workflow-image {
      width: 80%;
    }
  }

  .right {
    flex: 1;

    .title {
      padding: 0;
      margin: 0;
      font-weight: 800;
      color: #5A5454;
      font-size: 20px;
    }

    .desc {
      margin: 0;
      padding: 0;
      color: #747474;
      margin-top: 1rem;
      font-size: 20px;
    }

    .workflow-list {
      display: flex;
      margin-top: 2rem;

      .column {
        flex: 1;

        .item {
          display: flex;
          align-items: center;
          margin-top: 2rem;

          .number {
            display: flex;
            height: 65px;
            width: 65px;
            color: #fff;
            border-radius: 50%;
            align-items: center;
            font-size: 18px;
            justify-content: center;
          }

          .info {
            display: flex;
            flex-direction: column;
            margin-left: 1.5rem;
            width: 200px;

            .item-title {
              margin: 0;
              padding: 0;
              color: #747474;
              font-weight: normal;
            }

            .item-desc {
              margin: 0;
              padding: 0;
              margin-top: 0.3rem;
              color: #747474;
            }
          }


        }
      }


    }
  }

}


.blog-section {
  padding: 5rem;
  background-color: #FBFCFD;
  
  .title {
    margin: 0;
    padding: 0;
    color: #5A5454;
    font-size: 15px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
  }

  .desc {
    margin: 0;
    padding: 0;
    margin-top: 0.5rem;
    color: #747474;
    text-align: center;
  }

  .partners {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  .blog-posts {
    display: flex;
    justify-content: center;
    margin-top: 2rem;

    .blog-item {
      box-shadow: #c5c3f94a 1px 1px 10px 1px;
      width: 300px;
      margin: 1rem;

      .blog-thumb {
        width: 300px;
        height: 220px;
      }

      .blog-content {
        padding: 0 2rem;
        padding-bottom: 2rem;
        margin-top: 1rem;

        .title {
          margin: 0;
          font-size: 16px;
          padding: 0;
          text-align: left;
          font-weight: bold;
        }

        .desc {
          margin: 0;
          padding: 0;
          text-align: left;
          margin-top: 1rem;
          font-size: 14px;
        }

        .icon {
          width: 30px;
          margin-top: 1.5rem;
          
          &:hover {
            cursor: pointer;
          }
        }

      }

    }
  }
}


.footer {
  background-color: #fff;
  padding: 5rem;
  display: flex;
  justify-content: space-evenly;


  .our-info {
    width: 300px;
  }

  .desc {
    color: #747474;
  }

  .social-media {
    display: flex;

    .item {
      background: #0A315A;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      margin: 1rem 0.5rem;

      .icon {
        width: 20px;
      }
    }
  }

  .copyright {
    margin: 0;
    padding: 0;
    color: #969CA2;
    margin-top: 2rem;
  }

  .link-list {

    .title {
      font-size: 17px;
      font-weight: 700;
      color: #747474;
    }

    .link {
      color: #4B647E;
      font-size: 15px;
      margin-top: 1rem;

      &:hover {
        cursor: pointer;
      }
    }
  }
}